    </section>
    <section class="centerPick">
      <div class="pagetitle">
        <h1>Vue3.2语法糖使用总结</h1>
        <div class="author-info-box">
          <span>Yabaizzz</span>
          <span>{{ getNowFormatDate() }}</span>
        </div>
      </div>
      <div class="markdown-body cache">
        <h3 id="one">一.概述</h3>
        <p class="author-info-tetx">
          在 <span>Vue2</span> 时期，组件里定义的各类变量、方法、计算属性等是分别存放到 <span>data</span>、
          <span>methods</span>、 <span>computed</span>等选项里，这样编写的代码不便于后期的查阅，查找一个业务逻辑需要在各个选项来回切换。
          <span>vue3.0</span>组合式API<span>setup</span>函数的推出就是为了解决这个问题，它让我们的逻辑关注点更加集中，语法也更加精简，但是当我们在使用
          <span>vue3.0</span>的语法就构建组件的时候，
          总是需要把外面定义的方法变量必须要return出去才能在<span>&#60;template&#62;</span>
          ，比较麻烦一些。<span>vue3.2</span>语法糖的出现以及一些新增的API，让我们的代码进一步简化。
        </p>
        <h4 id="two">什么是语法糖？</h4>
        <p class="author-info-tetx">
          <strong>语法糖</strong>（英语：Syntactic
          sugar）是由英国计算机科学家彼得·兰丁发明的一个术语，指计算机语言中添加的某种语法，这种语法对语言的功能没有影响，但是更方便程序员使用。语法糖让程序更加简洁，有更高的可读性。
        </p>
        <h4 id="three"><span>Vue3.2</span>语法糖</h4>
        <p class="author-info-tetx">
          来看下<span>vue3.0</span>与<span>vue3.2</span>的单文件组件（SFC，即.vue 文件）的结构对比
        </p>
        <ul>
          <li class="author-info-tetx">
            <span>vue3.0</span>组件
          </li>
          <!--  代码模块  -->
          <sugar :code="vue2Dm"></sugar>
          <li class="author-info-tetx">
            <span>vue3.2</span>组件
          </li>
          <!--  代码模块  -->
          <sugar :code="vue3Dm"></sugar>
        </ul>
        <p class="author-info-tetx">
          1.&emsp;对比<span>vue3.0</span>与 <span>vue3.2</span>版本的组件模板，最主要的变化是3.2中没有了
          <span>setup</span>函数，而是把它放在了script标签中。
        </p>
        <p class="author-info-tetx">
          2.&emsp;我们定义的属性和方法也不用在return中返回，直接就可以用在模板语法中 ...
          <br/>
          这些是直观的变化，接下来我们学习具体的用法
        </p>
        <h3 id="fore">二.使用介绍</h3>
        <h4 id="five">1.组件注册</h4>
        <p class="author-info-tetx">
          <span>vue3.0</span>中使用组件，需要使用&emsp;components&emsp;选项来显式注册：
        </p>
        <sugar :code="comDm"></sugar>
        <p class="author-info-tetx">
          <span style="margin-right: 10px;">vue3.2</span>
          <span>&#60;script setup&#62;</span>
          的单文件组件中，导入的组件可以直接在模板中使用，组件会自动注册，并且无需指定当前组件的名字，它会自动以文件名为主，也就是不用再写name属性了。
        </p>
        <sugar :code="comSetDm"></sugar>
      </div>
    </section>
    <section class="rightPick">
      <div @click="go('#one')">概述</div>
      <div @click="go('#two')">什么是语法糖</div>
    </section>
  </div>
</template>

