export let vue3Dm = "<template>\n" +
    "    <MyTestVue :title='title' @click='changeTitle' />\n" +
    "</template>\n" +
    "<script setup>\n" +
    "import MyTestVue from './MyTest.vue';\n" +
    "import { ref } from 'vue';\n" +
    "const title = ref('测试一下')\n" +
    "const changeTitle = () => {\n" +
    "    title.value = 'Hello,World'\n" +
    "}\n" +
    "</script>\n" +
    "<style lang='scss' scoped>\n" +
    "</style>\n"


